import * as THREE from 'three'
import { TweenLite, Power4 } from 'gsap'

export function projectsAnimations() {
  //VARIABLES
  let itemsWrapper = document.querySelector('.projects')
  let isLoaded = false
  let timeSpeed
  let time
  let clock
  let container = document.getElementById('p-container')
  let wrapper = document.querySelector('.projects-wrapper')

  const viewport = {
    width: wrapper.clientWidth,
    height: wrapper.clientHeight,
    aspectRatio: wrapper.clientWidth / wrapper.clientHeight
  }
  //MATH
  Number.prototype.map = function (in_min, in_max, out_min, out_max) {
    return ((this - in_min) * (out_max - out_min)) / (in_max - in_min) + out_min
  }

  window.addEventListener('resize', onWindowResize.bind(this), false)
  let renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })
  renderer.setSize(viewport.width, viewport.height)
  renderer.setPixelRatio(window.devicePixelRatio)
  container.appendChild(renderer.domElement)
  let scene = new THREE.Scene()
  let camera = new THREE.PerspectiveCamera(40, viewport.aspectRatio, 0.1, 100)
  camera.position.set(0, 0, 3)

  let mouse = new THREE.Vector2()

  //TIME
  timeSpeed = 2
  time = 0
  clock = new THREE.Clock()
  //...............render.............
  function render() {
    // called every frame
    time += clock.getDelta() * timeSpeed
    renderer.render(scene, camera)
  }
  renderer.setAnimationLoop(render.bind(this))

  //...........ITEMS..............
  let items = [...itemsWrapper.querySelectorAll('.project')]
  items = items.map((item, index) => ({
    element: item,
    img: item.querySelector('img') || null,
    index: index
  }))

  //..........EVENT LISTENERS..............
  let tempItemIndex
  let isMouseOver
  let currentItem
  let options = {}

  //VIEW SIZE
  let distance = camera.position.z
  let vFov = (camera.fov * Math.PI) / 180
  const viewSize = {
    width: 2 * Math.tan(vFov / 2) * distance * viewport.aspectRatio,
    height: 2 * Math.tan(vFov / 2) * distance,
    vFov
  }

  items.forEach((item, index) => {
    item.element.addEventListener('mouseover', _onMouseOver.bind(this, index), false)
  })

  container.addEventListener('mousemove', _onMouseMove.bind(this), false)
  itemsWrapper.addEventListener('mouseleave', _onMouseLeave.bind(this), false)

  function _onMouseLeave(event) {
    isMouseOver = false
    onMouseLeave(event)
  }

  function _onMouseMove(event) {
    // get normalized mouse position on viewport
    mouse.x = (event.clientX / viewport.width) * 2 - 1
    mouse.y = -(event.clientY / viewport.height) * 2 + 1

    onMouseMove(event)
  }

  function _onMouseOver(index, event) {
    tempItemIndex = index

    onMouseOver(index, event)
  }

  //.............resize............
  function onWindowResize() {
    camera.aspect = viewport.aspectRatio
    camera.updateProjectionMatrix()
    renderer.setSize(viewport.width, viewport.height)
  }

  //......UPDATE TEXTURE..........
  function onMouseEnter() {
    if (!currentItem || !isMouseOver) {
      isMouseOver = true
      // show plane
      TweenLite.to(uniforms.uAlpha, 0.5, {
        value: 1,
        ease: Power4.easeOut
      })
    }
  }

  function onMouseLeave(event) {
    TweenLite.to(uniforms.uAlpha, 0.5, {
      value: 0,
      ease: Power4.easeOut
    })
  }

  function onMouseMove(event) {
    // project mouse position to world coodinates
    let x = mouse.x.map(-1, 1, -viewSize.width / 2, viewSize.width / 2)
    let y = mouse.y.map(-1, 1, -viewSize.height / 2, viewSize.height / 2)

    position = new THREE.Vector3(x, y, 0)
    TweenLite.to(plane.position, 1, {
      x: x,
      y: y,
      ease: Power4.easeOut,
      onUpdate: onPositionUpdate.bind(this)
    })
  }

  function onTargetChange(index) {
    // item target changed
    currentItem = items[index]
    if (!currentItem.texture) return

    // compute image ratio
    let imageRatio = currentItem.img.naturalWidth / currentItem.img.naturalHeight
    scale = new THREE.Vector3(imageRatio, 1, 1)
    uniforms.uTexture.value = currentItem.texture
    plane.scale.copy(scale)
  }

  function onMouseOver(index, e) {
    if (!isLoaded) return
    onMouseEnter()
    if (currentItem && currentItem.index === index) return
    onTargetChange(index)
  }

  function onPositionUpdate() {
    // compute offset
    uniforms.uOffset.value = plane.position.clone().sub(position).multiplyScalar(-options.strength)
  }

  //..........LOAD TEXTURE.......
  function loadTexture(loader, url, index) {
    // https://threejs.org/docs/#api/en/loaders/TextureLoader
    return new Promise((resolve, reject) => {
      if (!url) {
        resolve({ texture: null, index })
        return
      }
      // load a resource
      loader.load(
        // resource URL
        url,

        // onLoad callback
        (texture) => {
          resolve({ texture, index })
        },

        // onProgress callback currently not supported
        undefined,

        // onError callback
        (error) => {
          console.error('An error happened.', error)
          reject(error)
        }
      )
    })
  }

  //..........INIT..............
  options.strength = options.strength || 0.25

  let position = new THREE.Vector3(0, 0, 0)
  let scale = new THREE.Vector3(1, 1, 1)
  let geometry = new THREE.PlaneBufferGeometry(1, 1, 32, 32)
  let uniforms = {
    uTime: {
      value: 0
    },
    uTexture: {
      value: null
    },
    uOffset: {
      value: new THREE.Vector2(0.0, 0.0)
    },
    uAlpha: {
      value: 0
    }
  }
  let material = new THREE.ShaderMaterial({
    uniforms: uniforms,
    vertexShader: `
                uniform vec2 uOffset;
                varying vec2 vUv;
                vec3 deformationCurve(vec3 position, vec2 uv, vec2 offset) {
                  float M_PI = 3.1415926535897932384626433832795;
                  position.x = position.x + (sin(uv.y * M_PI) * offset.x);
                  position.y = position.y + (sin(uv.x * M_PI) * offset.y);
                  return position;
                }
                
                void main() {
                  vUv = uv;
                  vec3 newPosition = position;
                  newPosition = deformationCurve(position,uv,uOffset);
                  gl_Position = projectionMatrix * modelViewMatrix * vec4( newPosition, 1.0 );
                }
              `,
    fragmentShader: `
                uniform sampler2D uTexture;
                uniform float uAlpha;
                uniform vec2 uOffset;
                varying vec2 vUv;
                vec3 rgbShift(sampler2D texture, vec2 uv, vec2 offset) {
                  float r = texture2D(uTexture,vUv + uOffset).r;
                  vec2 gb = texture2D(uTexture,vUv).gb;
                  return vec3(r,gb);
                }
                void main() {
                  vec3 color = rgbShift(uTexture,vUv,uOffset);
                  gl_FragColor = vec4(color,uAlpha);
                }
              `,
    transparent: true
  })
  let plane = new THREE.Mesh(geometry, material)
  scene.add(plane)

  function initEffectShell() {
    let promises = []

    const THREEtextureLoader = new THREE.TextureLoader()

    items.forEach((item, index) => {
      // create textures
      promises.push(loadTexture(THREEtextureLoader, item.img ? item.img.src : null, index))
    })

    return new Promise((resolve, reject) => {
      // resolve textures promises
      Promise.all(promises).then((promises) => {
        // all textures are loaded
        promises.forEach((promise, index) => {
          // assign texture to item
          items[index].texture = promise.texture
        })
        resolve()
      })
    })
  }

  initEffectShell().then(() => {
    console.log('load finished')
    isLoaded = true
  })
}
